<template>
  <div>
    <el-row>
      <el-col :span="20" :offset="2">
        <el-tabs tab-position="left">
          <el-tab-pane label="">
            <div>
              <el-table :data="journalTableData" style="width: 100%">
                <el-table-column
                  header-align="center"
                  align="center"
                  label=""
                  prop="no"
                  width="60"
                ></el-table-column>
                <el-table-column
                  header-align="center"
                  align="center"
                  label=""
                  prop="name"
                  width="280"
                ></el-table-column>
                <el-table-column
                  header-align="center"
                  label=""
                  prop="introduce"
                ></el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-table :data="classResourceTableData">
              <el-table-column
                label=""
                header-align="center"
                align="center"
                prop="orderNum"
                width="60"
              ></el-table-column>
              <el-table-column
                label=""
                header-align="center"
                align="center"
                prop="name"
                width="200"
              ></el-table-column>
              <el-table-column
                label=""
                header-align="center"
                align="center"
                prop="teacher"
                width="100"
              ></el-table-column>
              <el-table-column
                label=""
                header-align="center"
                prop="target"
              ></el-table-column>
              <el-table-column
                label=""
                header-align="center"
                align="center"
                width="80"
              >
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="primary"
                    plain
                    @click="downloadClassResource(scope.$index, scope.row)"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="/">
            <div style="padding: 0 20px 0 20px">
              <h5></h5>
              <el-button
                plain
                @click="
                  download(
                    '/static/file/info-class-resource/Acrobat.9.Pro.ChS.rar'
                  )
                "
                >PDF</el-button
              >
              <el-button
                plain
                @click="
                  download(
                    '/static/file/info-class-resource/CAJViewer 7.2.self.exe'
                  )
                "
              >
                CAJ
              </el-button>

              <h5>/</h5>
              <el-link
                type="primary"
                @click="
                  download('/static/file/info-class-resource/.pdf')
                "
                ></el-link
              ><br />
              <el-link
                type="primary"
                @click="
                  download('/static/file/info-class-resource/.pdf')
                "
                ></el-link
              >
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getClassResource } from "@/api/manageClassResource";
import baseUrl from "@/config/baseUrl";
import { Message, MessageBox } from "element-ui";

export default {
  data() {
    return {
      baseUrl,
      journalTableData: [
        {
          no: 1,
          name: "Exlibris",
          introduce:
            "SFX  http://sfx-86ali.hosted.exlibrisgroup.com/index.html",
        },
        {
          no: 1,
          name: "CSSCI",
          introduce:
            "Chinese Social Science Citation Indexhttp://cssci.nju.edu.cn/",
        },
        {
          no: 1,
          name: "SCIE",
          introduce: "http://apps.webofknowledge.com/",
        },
        {
          no: 1,
          name: "SSCI",
          introduce: "http://apps.webofknowledge.com/",
        },
        {
          no: 1,
          name: "EI",
          introduce: "https://wwww.engineeringvillage.com/home.url",
        },
      ],
      classResourceTableData: [],
    };
  },
  methods: {
    download(url) {
      window.open(baseUrl + url);
    },
    downloadClassResource(id, row) {
      if (row.filePath != null && row.filePath != "")
        window.open(
          baseUrl + "/static/file/info-class-resource/" + row.filePath
        );
      else
        Message({
          message: "",
          type: "error",
        });
    },
  },
  created() {
    getClassResource({ page: 0, size: 1000 }).then((res) => {
      this.classResourceTableData = res.result;
    });
  },
};
</script>

