<script>
import { getDetailInfo } from "@/api/queryInformation";
import baseUrl from "@/config/baseUrl";
// import { briefReportInterior } from '../store/infoType';
// import { detailShow } from "@/store/infoType";
const base = [
  {
    title: "",
    field: "infoCategory",
  },
  {
    title: "",
    field: "professionField",
  },
  {
    title: "",
    field: "topicCategory",
  },
  {
    title: "",
    field: "publishDepartment",
  },
  {
    title: "",
    field: "checkInTime",
  },
  {
    title: "",
    field: "knowledgeType",
  },
  {
    title: "",
    field: "securityLevel",
  },
  {
    title: "",
    field: "formatTime",
  },
  {
    title: "",
    field: "language",
  },
  {
    title: "",
    field: "publishDepartment",
  },
  {
    title: "",
    field: "informationOrigin",
  },
  {
    title: "",
    field: "infoNo",
  },
];
const industryTrend = {
  type: "",
  url: "IndustryTrend",
  show: [
    {
      title: "",
      field: "title",
    },
    {
      title: "",
      field: "trendType",
    },
  ].concat(base),
};
const reportExterior = {
  type: "",
  url: "ReportExterior",
  show: [
    {
      field: "title",
      title: "",
    },
    {
      field: "type",
      title: "",
    },
    {
      field: "completeDepartment",
      title: "",
    },
  ].concat(base),
};
const briefReportExterior = {
  type: "",
  url: "BriefReportExterior",
  show: [
    {
      field: "name",
      title: "",
    },
    {
      field: "type",
      title: "",
    },
    {
      field: "completeDepartment",
      title: "",
    },
    {
      field: "title",
      title: "",
    },
  ].concat(base),
};
const patentExterior = {
  type: "",
  url: "PatentExterior",
  show: [
    {
      field: "inventionTitile",
      title: "",
    },
    {
      field: "applicationNo",
      title: "",
    },
    {
      field: "applicationDay",
      title: "",
    },

    {
      field: "publicationNo",
      title: "",
    },

    {
      field: "applicationType",
      title: "",
    },
    {
      field: "patentType",
      title: "",
    },
    {
      field: "assignee",
      title: "",
    },

    {
      field: "inventor",
      title: "",
    },
    {
      field: "claims",
      title: "",
    },
  ].concat(base),
};
const paperExterior = {
  type: "",
  url: "PaperExterior",
  show: [
    {
      field: "paperType",
      title: "",
    },
    {
      field: "title",
      title: "",
    },
    {
      field: "author",
      title: "",
    },
    {
      field: "authorInstitution",
      title: "",
    },
    {
      field: "firstAuthorInstitution",
      title: "",
    },
    {
      field: "informationComeFrom",
      title: "",
    },
    {
      field: "publicationTime",
      title: "",
    },

    {
      field: "documentType",
      title: "",
    },
    {
      field: "pageNo",
      title: "",
    },
  ].concat(base),
};
const standardExterior = {
  type: "",
  url: "StandardExterior",
  show: [
    {
      field: "name",
      title: "",
    },
    {
      field: "num",
      title: "",
    },
    {
      field: "publicateDate",
      title: "",
    },

    {
      field: "executeDate",
      title: "",
    },

    {
      field: "specifyInstitution",
      title: "",
    },

    {
      field: "status",
      title: "",
    },
    {
      field: "authorInstitution",
      title: "",
    },
    {
      field: "chineseStandardClassNum",
      title: "",
    },
    {
      field: "substituteStandard",
      title: "",
    },
  ].concat(base),
};
const briefReportInterior = {
  type: "",
  url: "BriefReportInterior",
  show: [
    {
      field: "name",
      title: "",
    },
    {
      field: "type",
      title: "",
    },
    {
      field: "completeDepartment",
      title: "",
    },
    {
      field: "title",
      title: "",
    },
  ].concat(base),
};
const reportInterior = {
  type: "",
  url: "ReportInterior",
  show: [
    {
      field: "title",
      title: "",
    },
    {
      field: "type",
      title: "",
    },
    {
      field: "completeDepartment",
      title: "",
    },
  ].concat(base),
};

const detailShow = [
  industryTrend,
  reportExterior,
  briefReportExterior,
  paperExterior,
  patentExterior,
  standardExterior,
  briefReportInterior,
  reportInterior,
];

export default {
  props: {
    originId: String,
    infoType: String,
    infoTitle: String,
    infoFileUrl: String,
  },
  data() {
    return {
      detailShow,
      info: [],
    };
  },
  methods: {
    openFile() {
      if (!this.infoFileUrl.includes(".")) {
        this.$message({
          type: "warning",
          message: "",
        });
      } else window.open(baseUrl + this.infoFileUrl);
    },
    getDetailShow() {
      return this.detailShow.find((item) => item.type == this.infoType).show;
    },
    getDetail() {
      getDetailInfo({
        infoType: this.detailShow.find((item) => item.type == this.infoType)
          .url,
        id: this.originId,
      }).then((res) => {
        this.info = res;
      });
    },
  },
  created() {
    // console.log(this.infoType);
    // console.log(this.detailShow.find((item) => item.type == this.infoType));
    // console.log(this.detailShow);
    this.getDetail();
  },
};
</script>

<style>
